footer {
  background: #112;
  width: 100%;
  height: 110px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  font-size: small;
}
footer span {
  margin: 0px 10px;
  margin-top: 5px;
  color: #556;
  text-align: center;
}
footer span:first-child {
  margin-top: 0px;
}